// Pull all the services from backend
// loop through and create a tab for each 
// pull in all case studies
// display case studies based on service slug (attached to service taxonomy)
<template v-if="services != null">
    <section class="tabs boxes grey-background">
        <ul class="responsive-tabs__list">	
            <li v-for="(service, index) in services" :key="'tab-'+index">
                <a href="#<?php echo $hash; ?>">
                    <div class="tab-icon" style="background-image:url(<?php echo $iconurl[0]; ?>);">
                    </div>
                    <span class="-ls-3" v-html="service.title.rendered"></span>
                </a>
            </li>     
        </ul>
        
        <div v-for="(service, index) in services" :key="'tab-content-'+index">

            <div class="grey-background">
                <div class="outer-container">
                    <div class="main-content">
                        <div class="tabs-panel-intro">
                            <div class="tabs-panel__left">
                                <img src="<?php echo $imageurl[0]; ?>" alt="">
                            </div>
                            <div class="tabs-panel__right">
                                <h2 v-html="service.title.rendered"></h2>
                                <div v-html="service.content.rendered"></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            
            <div class="grey-background">
                <div class="outer-container">
                    <div class="tabs-panel-pockets">	
                        <div class="service-pocket__single">
                            <h4><?php echo $heading; ?></h4>
                            <p><?php echo $text; ?></p>
                        </div>   
                    </div>
                </div>
            </div>
            
            <div class="services-testimonial">
            </div>

            <div class="services-case-studies" id="" data-cases="">
            </div>
            

        </div>

    </section>
</template>

<script>
    import store from './store';
    
    export default {
        store,
        data(){
            return {
            }
        },
        computed:{
            services() {
                return this.$store.getters['getServices']
            },
        },
        created() {
            this.$store.dispatch('apiServices');
            this.$store.dispatch('apiCaseStudies');
        },
        methods: {
            increment () {
                store.commit('increment')
            },
            decrement () {
                store.commit('decrement')
            }
        }
    }
</script>

<style lang="scss">
    @import './sass/base.scss';
</style>


